import React, {useRef, useState, useEffect, useMemo, useContext} from "react";
import styled from "styled-components";
import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import SyntaxHighlighter from "react-syntax-highlighter";
import { IsDarkContext } from "../../context/IsDarkContext";

const Main = styled.div`
    width: 90%;
    height: 100%;
    overflow-y: scroll;
    border-radius: 0;

    &::-webkit-scrollbar {
        width: 0;
    }

    &::-webkit-scrollbar {
        display: none;
    }
`;

const Margin = styled.div`
    height: 10%;
`;

const ScUpBtn = styled.img`
    position: absolute;
    bottom: 0;
    right: 0;
    width: 5%;
    &:hover{
        cursor: pointer;
    }
`;

function Project(props){
    const isDark = useContext(IsDarkContext);
    const contentRef = useRef();

    const handleScroll = () => {
        const currentScrollY = contentRef.current;
        props.scrollDown(currentScrollY.scrollTop > currentScrollY.prevScrollY);
        currentScrollY.prevScrollY = currentScrollY.scrollTop;
    };

    const scTop = () => {
        contentRef.current.scrollTop = 0;
    };
    
    if (props.proChanged){
        contentRef.current.scrollTop = 0;
        props.setProChanged(false);
    }
    
    const [markdown, setMarkdown] = useState("");

    const contain = [
        "what_to_eat.md",
        "star.md",
        "cyworld.md",
        "mini-maple.md"
    ];

    const fileCache = useMemo(() => ({}), []);

    useEffect(() => {
        const fileName = contain[props.num];
        if (!fileCache[fileName]) {
            fetch(`${process.env.PUBLIC_URL}/markdown/${fileName}`)
            .then((response) => response.text())
            .then((text) => {
                const formattedText = text.replaceAll("<br/>","\n\n");
                fileCache[fileName] = formattedText;
                setMarkdown(formattedText);
            });
        } else {
            setMarkdown(fileCache[fileName]);
        }
    }, [props.num]);

    const renderers = useMemo(() => ({
        code({ node, inline, className, children, ...props }) {
            const match = /language-(\w+)/.exec(className || '');
            return(
                (!inline && match)
                ? <SyntaxHighlighter
                    {...props}
                    language={match[1]}
                    className = "codeRefer"
                >
                    {String(children).replace(/\n$/, '')}
                </SyntaxHighlighter>
                : <SyntaxHighlighter
                    {...props}
                    className = "backtick"
                >
                    {String(children)}
                </SyntaxHighlighter>
            );
        }
    }), []);

    return (
        <Main
            ref={contentRef}
            onScroll={handleScroll}
        >
            <Margin />
            <ReactMarkdown
                className = {isDark ? "markdown dark" : "markdown"}
                remarkPlugins={[remarkGfm]}
                components={renderers}
            >
                {markdown}
            </ReactMarkdown>
            <ScUpBtn
                src = {isDark?`${process.env.PUBLIC_URL}/images/uArrowW.png`:`${process.env.PUBLIC_URL}/images/uArrow.png`}
                onClick={()=>scTop()}
            />
        </Main>
    );
}

export default Project;